.podr-map {
  position: relative;
  overflow: hidden;
  min-height: 690px;
  &__map {
    min-height: 690px;
    width: 100%;
    background: url(../img/map.jpg) center center no-repeat;
    background-size: cover;
    position: absolute;
    left: 0;
    top: 0; }
  .container {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    max-width: 1280px; }
  &__offices {
    position: absolute;
    z-index: 3;
    top: 50px;
    right: 40px;
    max-width: 460px;
    background: #fff;
    box-shadow: 6.356px 12.474px 47.94px 3.06px rgba(0, 0, 0, 0.24);
    border: 1px solid rgba(54, 54, 54, 0.2); }
  &__head {
    padding: 25px 25px 18px;
    border-bottom: 1px solid #e4e4e4; }
  &__buttons {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    border-bottom: 3px solid #e4e4e4;
    &--editable {
      justify-content: flex-start;
      width: auto;
      .podr-map__btn {
        margin-right: 15px; } } }
  &__head-tab {
    display: none;
    &.is-active {
      display: block; } }
  &__head-grid {
    padding: 10px 26px;
    background: #f0f0f0;
    border-bottom: 1px solid #c4c4c4;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap; }
  &__see {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 10px;
    p {
      font-size: 14px;
      color: $fontColor;
      font-family: $light; } }
  &__img {
    font-size: 0;
    padding-right: 15px;
    img {
      height: 25px;
      width: auto; } }
  &__images {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-top: 5px; }
  &__btn {
    font-size: 16px;
    font-family: $bold;
    color: $fontColor;
    text-transform: uppercase;
    transition: all $transitionSpeed $ease;
    border-bottom: 3px solid #e4e4e4;
    padding-bottom: 13px;
    margin-bottom: -3px;
    opacity: 0.7;
    &.is-active {
      opacity: 1;
      border-color: $hoverRed; } }
  &__scroll {
    width: calc(100% + 30px); }
  &__body {
    overflow: hidden;
    position: relative;
    min-height: 520px; }

  &__tab {
    max-height: 450px;
    overflow: hidden;
    overflow-y: scroll;
    display: none;
    &.is-active {
      display: block; } }
  &__item {
    padding: 20px 46px;
    transition: all $transitionSpeed $ease;
    display: inline-block;
    width: 100%;
    line-height: 1.3;
    span {
      font-size: 18px;
      color: $fontColor;
      display: inline-block;
      width: 100%;
      line-height: 1.3;
      &.city {
        font-family: $bold; }
      &.phone {
        font-family: $light; } }
    &:hover {
      background: rgba(0,0,0,0.05); }
    &--editable {
      padding: 10px 35px;
      border-bottom: 1px solid #c4c4c4;
      &:last-child {
        border-bottom: none; } } }
  &__slider {
    width: 100%;
    max-height: 300px;
    overflow: hidden;
    position: relative;
    z-index: 15; }
  &__image {
    min-height: 300px;
    position: relative;
    img {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      width: 100%;
      max-width: 500px;
      height: auto; } }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    background: #fff;
    width: 30px;
    height: 30px;
    cursor: pointer;
    z-index: 16;
    &:before {
      width: 2px;
      height: 22px;
      background: $hoverRed;
      transform: rotate(45deg);
      position: absolute;
      display: block;
      content: '';
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg); }
    &:after {
      width: 2px;
      height: 22px;
      background: $hoverRed;
      transform: rotate(45deg);
      position: absolute;
      display: block;
      content: '';
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(-45deg); } }
  &__detail-ready {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    max-width: 460px;
    min-height: 520px;
    opacity: 0;
    pointer-events: none;
    transition: all $transitionSpeed $ease;
    &.is-active {
      opacity: 1;
      pointer-events: all; }
    &:before {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 5000px;
      display: block;
      content: '';
      background: #fff; } }
  &__detail-proccess {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    max-width: 460px;
    min-height: 520px;
    opacity: 0;
    pointer-events: none;
    transition: all $transitionSpeed $ease;
    &.is-active {
      opacity: 1;
      pointer-events: all; }
    &:before {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 5000px;
      display: block;
      content: '';
      background: #fff; } }

  &__detail-row {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 10px 35px;
    flex-wrap: wrap;
    border-bottom: 1px solid #c4c4c4;
    &:last-child {
      border-bottom: none; }
    p {
      padding: 5px 0;
      font-family: $medium;
      font-size: 16px;
      padding-right: 15px;
      margin-right: auto; } }
  &__detail-master {
    padding: 5px 0;
    display: inline-block;
    span {
      font-family: $light;
      border-bottom: 1px solid $fontColor;
      color: $fontColor;
      font-size: 16px;
      text-transform: uppercase; } }
  &__detail-info {
    background: #fff;
    position: relative;
    z-index: 15; }
  &__detail-head {
    padding: 10px 35px;
    border-bottom: 1px solid #c4c4c4; }

  &__detail-title {
    font-size: 16px;
    font-family: $bold;
    color: $fontColor;
    line-height: 1.2; }
  &__detail-images {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 7px; }
  &__detail-image-item {
    display: flex;
    align-items: center;
    padding-right: 15px;
    p {
      font-size: 18px;
      font-family: $bold;
      margin-right: 10px;
      line-height: 1;
      span {
        font-family: $regular; } } }
  &__detail-img {
    font-size: 0;
    padding-right: 10px;
    img {
      height: 18px;
      width: auto; } }
  .slick-arrow {
    position: absolute;
    width: 35px;
    height: 35px;
    background: rgba($fontColor, 0.85);
    transition: all $transitionSpeed $ease;
    top: 50%;
    transform: translateY(-50%);
    border: none;
    font-size: 0;
    color: transparent;
    cursor: pointer;
    z-index: 2;
    &:hover {
      background: rgba($fontColor, 0.95); }
    &.slick-prev {
      left: 0px;
      &:before {
        border-top: none;
        border-right: none;
        left: 57%; } }
    &.slick-next {
      right: 0px;
      &:before {
        border-bottom: none;
        border-left: none;
        left: 43%; } }
    &:before {
      display: block;
      position: absolute;
      content: '';
      width: 11px;
      height: 11px;
      border: 2px solid #fff;
      transform: translate(-50%, -50%) rotate(45deg);
      top: 50%;
      left: 50%; } }
  .slick-dots {
    position: absolute;
    margin: 0 auto;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    line-height: 0;
    height: auto;
    font-size: 0;
    padding: 0;
    li {
      margin: 0 8px;
      &.slick-active {
        button {
          background: $hoverRed; } } }
    button {
      font-size: 0;
      padding: 0;
      margin: 0;
      line-height: 0;
      background: #e9e9e9;
      width: 7px;
      height: 7px;
      border-radius: 50%;
      border: none;
      transition: all $transitionSpeed $ease; } }
  &__link-page {
    font-size: 13px;
    font-family: $montserrat;
    font-weight: 500;
    color: #999999;
    text-transform: none;
    width: auto;
    background: #fff;
    padding: 3px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all $transitionSpeed $ease;
    margin-left: auto;
    span {
      display: inline-block;
      width: 10px;
      height: 10px;
      border: 2px solid #bcbcbc;
      transform: rotate(45deg);
      border-bottom: none;
      border-left: none;
      margin-left: 10px;
      transition: all $transitionSpeed $ease; }
    &:hover {
      color: $fontColor;
      span {
        border-color: $fontColor; } } } }

@include r($tablet) {
  .podr-map {
    display: flex;
    flex-direction: column-reverse;
    padding-top: 30px;
    &__map {
      position: static; }
    &__offices {
      order: -1;
      position: static;
      max-width: 100%;
      margin: 0 auto 30px; }
    &__item {
      padding: 10px 55px 10px 25px;
      span {
        font-size: 16px; }
      &--editable {
        padding: 12px 55px 12px 20px; } }
    &__head-grid {
      padding: 5px 10px; }
    &__img {
      min-width: 35px;
      padding-right: 7px;
      img {
        height: 20px; } }
    &__detail-head {
      padding: 10px 20px; }
    &__detail-row {
      padding: 10px 20px; } } }

@include r($mobile) {
  .podr-map {
    &__head {
      padding: 15px; }
    &__btn {
      font-size: 11px; } } }
