.podr-info {
  background: #fff;
  padding: 120px 0 103px;
  p {
    font-size: 20px;
    color: $fontColor;
    line-height: 1.20;
    font-family: $light; }
  &--card {
    padding: 85px 0 79px;
    p {
      font-size: 19px;
      line-height: 1.25;
      margin-top: 30px;
      &.title {
        margin-top: 0;
        font-size: 43px;
        font-family: $medium;
        span {
          font-family: $extrabold; } } } } }

@include r($desktop) {
  .podr-info {
    padding: 75px 0; } }

@include r($tablet) {
  .podr-info {
    padding: 40px 0;
    p {
      font-size: 16px; }
    &--card {
      padding: 40px 0;
      p {
        margin-top: 20px;
        &.title {
          font-size: 24px; } } } } }
