.p-system {
  padding: 64px 0 68px;
  background: #fff;
  &__title {
    font-size: 48px;
    color: $fontColor;
    line-height: 1;
    font-family: $medium;
    letter-spacing: 0.2px;
    width: 100%;
    text-align: left; }
  &__grid {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-top: 37px; }
  &__image {
    flex: 1 0 65px;
    margin-right: 36px;
    font-size: 0;
    img {
      width: 65px;
      height: auto;
      opacity: 0.7; } }
  &__info {
    width: 100%;
    p {
      font-size: 18px;
      font-family: $light;
      line-height: 1.3;
      margin-bottom: 45px;
      &:last-child {
        margin-bottom: 10px; } } }
  &__item {
    border-bottom: 1px solid #c4c4c4;
    padding: 10px 25px 10px 78px;
    display: flex;
    align-items: center;
    p {
      font-size: 20px;
      color: $fontColor;
      line-height: 1.5; } }
  &__item-image {
    max-width: 100px;
    width: 100%;
    text-align: center;
    margin-right: 20px;
    img {
      margin: 0 5px;
      height: 40px;
      width: auto; } } }

@include r($desktop) {
  .p-system {
    &__item {
      padding-left: 15px; } } }

@include r($tablet) {
  .p-system {
    padding: 40px 0;
    &__image {
      flex: 1 0 55px;
      margin-right: 25px;
      img {
        width: 55px; } }
    &__info {
      p {
        font-size: 16px;
        margin-bottom: 25px;
        &:last-child {
          margin-left: -80px; } } }
    &__item {
      padding: 7px 15px 7px 0;
      p {
        font-size: 15px;
        line-height: 1.3; } }
    &__item-image {
      margin-right: 10px;
      max-width: 80px;
      width: 100%;
      img {
        height: 30px; } }
    &__item-info {
      width: calc(100% - 80px); } } }
