.blog {
  padding: 57px 0 60px;
  border: 1px solid #bcbcbc;
  border-left: none;
  border-right: none;
  background: #e9e9e9;
  .container {
    max-width: 1200px;
    padding: 0 100px; }
  &__title {
    font-size: 48px;
    color: $fontColor;
    line-height: 1;
    font-family: $medium;
    width: 100%;
    text-align: left; }
  &__slider {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 42px; }
  &__item {
    width: 32%;
    max-width: 320px;
    transition: all $transitionSpeed $ease; }
  &__item-container {
    transition: all $transitionSpeed $ease;
    &:hover {
      box-shadow: 0px 0px 25px rgba(0,0,0,0.3); } }
  &__image {
    // min-height: 320px
    max-height: 320px;
    max-width: 320px;
    width: 100%;
    overflow: hidden;
    font-size: 0;
    img {
      width: 100%;
      height: auto; } }
  &__info {
    background: #fff;
    padding: 30px 30px 23px; }
  &__i-title {
    font-size: 16px;
    font-family: $extrabold;
    text-transform: uppercase;
    color: $hoverRed;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    overflow: hidden;
    display: block;
    display: -webkit-box;
    letter-spacing: 0.2px;
    line-height: 1.2;
    max-height: 54px;
    min-height: 54px;
    cursor: pointer; }
  &__about {
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    display: block;
    overflow: hidden;
    display: -webkit-box;
    letter-spacing: 0.3px;
    font-family: $regular;
    font-size: 14px;
    line-height: 1.3;
    min-height: 54px;
    max-height: 54px;
    margin-top: 25px; }
  .btn {
    margin-top: 20px;
    font-size: 13px;
    font-family: $montserrat;
    font-weight: 500;
    color: #999999;
    text-transform: none;
    width: auto;
    margin-left: auto;
    background: #fff;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 125px;
    cursor: pointer;
    span {
      width: 10px;
      height: 10px;
      border: 2px solid #bcbcbc;
      transform: rotate(45deg);
      border-bottom: none;
      border-left: none;
      margin-left: 10px;
      transition: all $transitionSpeed $ease; }
    &:hover {
      color: $hoverRed;
      span {
        border-color: $hoverRed; } } }
  &__button {
    display: flex;
    margin: 43px auto 0;
    padding: 18px 25px 15px;
    font-size: 16px;
    text-transform: uppercase;
    font-family: $medium;
    color: $fontColor;
    border: 1px solid $fontColor;
    transition: all $transitionSpeed $ease;
    max-width: 270px;
    align-items: center;
    justify-content: center;
    line-height: 1;
    &:hover {
      background: $hoverRed;
      border-color: $hoverRed;
      color: #fff; } } }


@include r($desktop1068) {
  .blog {
    padding: 80px 0 100px;
    &__title {
      margin-left: 0;
      font-size: 36px;
      max-width: 400px; }
    .container {
      padding: 0 40px; } } }


@include r($tablet) {
  .blog {
    padding: 40px 0 20px;
    .container {
      padding: 0; }
    &__title {
      font-size: 24px;
      text-align: left;
      margin: 0 auto;
      font-weight: 600;
      max-width: 100%;
      padding-left: 20px; }
    &__slider {
      margin-top: 60px;
      text-align: center;
      max-width: 320px;
      margin: 20px auto 60px;
      position: relative; }
    &__i-title {
      text-align: left; }
    &__about {
      text-align: left; }
    &__item {
      margin-top: 20px;
      margin-bottom: 20px;
      padding: 0 20px; }
    .slick-dots {
      position: absolute;
      margin: 0 auto;
      bottom: -30px;
      left: 50%;
      transform: translateX(-50%);
      line-height: 0;
      height: auto;
      font-size: 0;
      padding: 0;
      li {
        margin: 0 8px;
        &.slick-active {
          button {
            background: $hoverRed; } } }
      button {
        font-size: 0;
        padding: 0;
        margin: 0;
        line-height: 0;
        background: #fff;
        width: 7px;
        height: 7px;
        border-radius: 50%;
        border: none;
        transition: all $transitionSpeed $ease; } }
    &__button {
      margin-top: 20px;
      font-size: 14px;
      padding: 21px 25px 19px;
      max-width: 320px; }
    .slick-arrow {
      position: absolute;
      background: rgba($fontColor, 0.3);
      transition: all $transitionSpeed $ease;
      border: none;
      font-size: 0;
      color: transparent;
      cursor: pointer;

      top: auto;
      bottom: -43px;
      transform: translateY(0);
      width: 35px;
      height: 35px;
      &:hover {
        background: rgba($fontColor, 0.7); }
      &.slick-prev {
        left: 0px;
        &:before {
          border-top: none;
          border-right: none;
          left: 57%; } }
      &.slick-next {
        right: 0px;
        &:before {
          border-bottom: none;
          border-left: none;
          left: 43%; } }
      &:before {
        display: block;
        position: absolute;
        content: '';
        width: 10px;
        height: 10px;
        border: 2px solid #fff;
        transform: translate(-50%, -50%) rotate(45deg);
        top: 50%;
        left: 50%; } } } }
