.want-form {
  padding: 62px 0 0;
  background: #514d4b;
  &__container {
    background: url(../img/bg-form.png) center bottom no-repeat;
    margin-bottom: -3px;
    position: relative; }
  .container {
    max-width: 1056px;
    padding-bottom: 62px;
    position: relative; }
  &__lines {
    position: absolute;
    bottom: 0;
    right: -290px;
    width: 200px;
    height: 60px; }
  &__top {
    position: absolute;
    width: 8000px;
    height: 28px;
    left: 0;
    top: 0;
    background: #d5d5d5;
    overflow: hidden;
    z-index: 3;
    &:before {
      position: absolute;
      display: block;
      content: '';
      background: #514d4b;
      left: 0;
      top: 0;
      border-color: transparent #d5d5d5 #d5d5d5 transparent;
      border-style: solid;
      border-width: 14px 15px 14px 15px;
      height: 0px;
      width: 0px; } }
  &__bottom {
    position: absolute;
    width: 8000px;
    height: 35px;
    left: 0;
    bottom: -0px;
    background: #f4f4f4;
    z-index: 2;
    &:before {
      position: absolute;
      display: block;
      content: '';
      background: #514d4b;
      left: -28px;
      top: 1px;
      border-color: transparent #f4f4f4 #f4f4f4 transparent;
      border-style: solid;
      border-width: 14px 15px 14px 15px;
      height: 0px;
      width: 0px; }
    &:after {
      position: absolute;
      display: block;
      content: '';
      background: #f4f4f4;
      left: -28px;
      bottom: 5px;
      height: 1px;
      width: 30px; } }

  &__title {
    font-size: 40px;
    color: #ffffff;
    font-family: $bold;
    span {
      font-family: $extrabold; } }
  &__personal {
    font-size: 10px;
    font-family: $light;
    color: rgba(255,255,255,0.5);
    display: inline-block;
    margin-top: 15px;
    a {
      display: inline;
      border-bottom: 1px solid rgba(255,255,255,0.5);
      transition: all $transitionSpeed $ease;
      &:hover {
        border-color: transparent; } } }
  &__grid {
    display: flex;
    align-items: center;
    justify-content: space-betweens;
    margin-top: 37px; }
  .input-container {
    width: 23%;
    margin-right: 1.5%;
    input {
      padding: 14px 15px; }

    &:nth-child(3) {
      width: 27%; }
    &:last-child {
      width: 23%;
      margin-right: 0;
      margin-left: 2%;
      input {
        padding: 15px 15px; } } } }

@include r($desktop) {
  .want-form {
    padding-top: 40px;
    .container {
      padding-bottom: 40px; }
    &__title {
      font-size: 28px;
      span {
        font-size: 36px; } }
    &__grid {
      flex-wrap: wrap;
      margin: 25px -10px 0;
      width: calc(100% + 20px); }
    .input-container {
      width: calc(50% - 20px);
      margin: 10px;
      &:nth-child(3) {
        width: calc(50% - 20px); }
      &:last-child {
        width: calc(50% - 20px);
        margin-left: 10px;
        margin-right: 10px; } } } }

@include r($mobile) {
  .want-form {
    padding-top: 30px;
    .container {
      padding-bottom: 30px; }
    &__title {
      font-size: 24px;
      span {
        font-size: 28px; } }
    &__grid {
      flex-wrap: wrap;
      margin: 10px 0 0;
      width: 100%; }
    .input-container {
      width: 100%;
      margin: 10px 0;
      &:nth-child(3) {
        width: 100%; }
      &:last-child {
        width: 100%;
        margin-left: 0;
        margin-right: 0;
        margin-bottom: 0; } } } }
