.blog--page {
  padding-bottom: 86px;
  border-left: none;
  border-right: none;
  background: #f0f0f0;
  .container {
    max-width: 1200px;
    padding: 0 100px; }
  &__title {
    font-size: 60px;
    color: $fontColor;
    line-height: 1;
    font-family: $medium;
    width: 100%;
    text-align: left; }
  &__slider {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap; }
  &__item {
    width: 32%;
    max-width: 320px;
    transition: all $transitionSpeed $ease;
    margin: 11px 0 12px; }
  &__item-container {
    transition: all $transitionSpeed $ease;
    &:hover {
      box-shadow: 0px 0px 25px rgba(0,0,0,0.3); } }
  &__image {
    // min-height: 320px
    max-height: 320px;
    max-width: 320px;
    width: 100%;
    overflow: hidden;
    font-size: 0;
    position: relative;
    img {
      width: 100%;
      height: auto;
      transition: all $transitionSpeed $ease; }
    a {
      position: absolute;
      width: 100%;
      height: 100%;
      display: block;
      top: 0;
      left: 0;
      background-image: url(../img/play.svg);
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 120px 120px; }
    &--link {
      &:hover {
        img {
          transform-origin: center center;
          transform: scale(1.1); } } } }
  &__info {
    background: #fff;
    padding: 25px 17px 23px; }
  &__i-title {
    font-size: 16px;
    font-family: $extrabold;
    text-transform: uppercase;
    color: $fontColor;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    overflow: hidden;
    display: block;
    display: -webkit-box;
    letter-spacing: 0.2px;
    line-height: 1.2;
    max-height: 74px;
    min-height: 74px;
    cursor: pointer;
    span {
      color: $hoverRed; } }
  &__head {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 20px; }
  &__head-tab {
    display: inline-block;
    padding: 7px 5px 5px;
    font-size: 14px;
    font-family: $bold;
    color: #ffffff;
    background: #cbcbcb;
    text-transform: uppercase;
    transition: all $transitionSpeed $ease;
    &:hover {
      background: $hoverRed; } }
  &__head-date {
    margin-left: 15px;
    font-family: $bold;
    color: $fontColor;
    font-size: 14px;
    display: inline-block; }
  &__about {
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    display: block;
    overflow: hidden;
    display: -webkit-box;
    letter-spacing: 0.3px;
    font-family: $regular;
    font-size: 14px;
    line-height: 1.3;
    min-height: 54px;
    max-height: 54px;
    margin-top: 25px; }
  .btn {
    margin-top: 20px;
    font-size: 13px;
    font-family: $montserrat;
    font-weight: 500;
    color: #999999;
    text-transform: none;
    width: auto;
    margin-left: auto;
    background: #fff;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 125px;
    cursor: pointer;
    span {
      width: 10px;
      height: 10px;
      border: 2px solid #bcbcbc;
      transform: rotate(45deg);
      border-bottom: none;
      border-left: none;
      margin-left: 10px;
      transition: all $transitionSpeed $ease; }
    &:hover {
      color: $hoverRed;
      span {
        border-color: $hoverRed; } } }

  .pagination {
    margin-top: 83px; }
