  &__buttons {
    display: flex;
    font-size: 0;
    align-items: center;
    margin-top: 24px;
    margin-bottom: 43px;
    position: relative;
    max-width: 100%; }
  &__tab {
    font-size: 25px;
    text-transform: uppercase;
    color: $fontColor;
    font-family: $bold;
    letter-spacing: 0;
    margin-right: 38px;
    padding-bottom: 10px;
    border-bottom: 3px solid transparent;
    opacity: 0.7;
    transition: all $transitionSpeed $ease;
    position: relative;
    z-index: 2;
    &:last-child {
      margin-right: 0;
      &:before {
        width: 100%; } }
    &:before {
      display: block;
      position: absolute;
      content: '';
      width: calc(100% + 38px);
      height: 3px;
      background: #dfdfdf;
      left: 0;
      bottom: -3px;
      z-index: 1;
      transition: all $transitionSpeed $ease; }
    &:after {
      display: block;
      position: absolute;
      content: '';
      width: 100%;
      height: 3px;
      background: #c6142d;
      left: 0;
      bottom: -3px;
      z-index: 2;
      opacity: 0;
      transition: all $transitionSpeed $ease; }
    &:hover {
      opacity: 1;
      border-color: #c6142d;
      &:after {
        opacity: 1; } }
    &.is-active {
      opacity: 1;
      border-color: #c6142d;
      &:before {
        opacity: 0.7; }
      &:after {
        opacity: 1; } } } }

@include r($desktop1068) {
  .blog--page {
    &__title {
      margin-left: 0;
      font-size: 36px;
      max-width: 400px; }
    .container {
      padding: 0 40px; } } }

@include r($desktop) {
  .blog--page {
      &__buttons {
        margin-top: 10px;
        margin-bottom: 25px; }
      &__tab {
        font-size: 20px;
        margin-right: 25px; } } }




@include r($tablet) {
  .blog--page {
    padding-bottom: 40px;
    .container {
      padding: 0 10px; }
    &__buttons {
      margin-top: 10px;
      margin-bottom: 25px;
      padding: 0 10px;
      flex-direction: column; }

    &__tab {
      width: 100%;
      font-size: 18px;
      margin-right: 0;
      padding: 12px 0 7px;
      text-align: center;

      border-bottom: 2px solid #dfdfdf;
      &:hover {
        background: rgba(106, 70, 75, 0.1);
        border-color: #dfdfdf !important; }
      &.is-active {
        background: #c6142d;
        color: #fff;
        &:hover {
          border-color: #c6142d !important; } }
      &:before,
      &:after {
        display: none; } }
    &__title {
      font-size: 26px;
      text-align: center;
      margin: 0 auto; }
    &__slider {
      text-align: center;
      justify-content: center; }
    &__i-title {
      text-align: left; }
    &__about {
      text-align: left; }
    &__item {
      margin: 10px;
      min-width: 250px;
      width: calc(50% - 20px); }
    .pagination {
      margin-top: 40px;
      padding: 0 10px;
      &__item {
        font-size: 12px;
        margin: 0 2px;
        min-width: 24px;
        max-width: 24px;
        width: 24px;
        min-height: 24px;
        max-height: 24px;
        height: 24px; }
      &__list {
        margin-left: 0; }
      a {
        &.prev,
        &.next {
          span.info {
            display: none; } }
        &.prev {
          padding-left: 0;
          span.arrow {
            margin-right: 5px; } }
        &.next {
          padding-right: 0;
          span.arrow {
            margin-left: 5px; } } } } } }

@include r($mobile) {
  .blog--page {
    .container {
      padding: 0 20px; }
    &__item {
      min-width: 0;
      max-width: 320px;
      width: 100%;
      margin: 10px 0; } } }
