.typical {
  &__buttons {
    display: flex;
    font-size: 0;
    align-items: center;
    margin-top: 7px;
    margin-bottom: 43px;
    position: relative;
    max-width: 100%;
    border-bottom: 3px solid #dfdfdf;
    margin-bottom: 50px; }
  &__btn {
    font-size: 25px;
    text-transform: uppercase;
    color: $fontColor;
    font-family: $bold;
    letter-spacing: 0;
    margin-right: 38px;
    padding: 10px;
    border-bottom: 3px solid transparent;
    opacity: 0.7;
    transition: all $transitionSpeed $ease;
    position: relative;
    z-index: 2;
    margin-bottom: -3px;
    &:last-child {
      margin-right: 0;
      margin-left: auto; }
    &:after {
      display: block;
      position: absolute;
      content: '';
      width: 100%;
      height: 3px;
      background: #c6142d;
      left: 0;
      bottom: -3px;
      z-index: 2;
      opacity: 0;
      transition: all $transitionSpeed $ease; }
    &:hover {
      opacity: 1;
      // border-color: #c6142d
      background: rgba(106, 70, 75, 0.1);
      // border-color: #dfdfdf !important
      // &:after
 }      //   opacity: 1
    &.is-active {
      opacity: 1;
      border-color: #c6142d;
      background: #c6142d;
      color: #fff;
      &:after {
        opacity: 1; } } } }

@include r($desktop) {
  .typical {
    &__buttons {
      margin-top: 10px;
      margin-bottom: 25px; }
    &__btn {
      font-size: 20px;
      margin-right: 25px; } } }

@include r($tablet) {
  .typical {
    &__buttons {
      margin-top: 10px;
      margin-bottom: 25px;
      // padding: 0 10px
      flex-direction: column;
      border-bottom: none; }

    &__btn {
      width: 100%;
      font-size: 18px;
      margin-right: 0;
      padding: 14px 0 9px;
      text-align: center;
      border-bottom: 2px solid #dfdfdf;
      margin-bottom: 0; } } }
