.general {
  min-height: 582px;
  &__grid {
    display: flex;
    align-items: flex-start;
    justify-content: space-between; }
  &__right {
    max-width: 465px;
    width: 42%; }
  &__title {
    font-size: 30px;
    color: $fontColor;
    line-height: 1;
    font-family: $medium;
    padding: 2px 5px 5px;
    border-bottom: 2px solid #c6142d; }
  &__row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 2px;
    padding: 14px 15px 9px 7px;
    line-height: 1;
    border-bottom: 1px solid rgba(#cacaca, 0.5);
    flex-wrap: wrap;
    p {
      font-size: 16px;
      color: $fontColor;
      font-family: $light;
      margin-right: 15px;
      &.bold {
        font-family: $medium;
        margin-left: auto;
        margin-right: 0;
        max-width: 240px;
        text-align: right; } }
    &--price {
      padding: 20px 15px 20px 7px;
      p {
        font-size: 18px;
        font-family: $regular;
        max-width: 140px;
        &.price {
          font-size: 36px;
          font-family: $bold;
          color: #c6142d;
          margin-left: auto;
          margin-right: 0;
          min-width: 280px;
          max-width: 280px;
          width: 100%;
          text-align: right; } } } }
  &__view {
    width: 100%;
    margin-top: 10px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    text-align: center; }
  &__view-image {
    font-size: 0;
    max-height: 55px;
    border-radius: 10px;
    overflow: hidden;
    max-width: 82px;
    position: relative;
    transition: all $transitionSpeed $ease;
    &:after {
      position: absolute;
      display: block;
      content: '';
      width: calc(100% - 2px);
      height: calc(100% - 2px);
      // border: 3px solid #c6142d
      border: 1px solid #cacaca;
      top: 0;
      left: 0;
      z-index: 2;
      border-radius: 10px;
      transition: all $transitionSpeed $ease;
      opacity: 1; }
    &:before {
      position: absolute;
      display: block;
      content: '';
      width: calc(100% - 6px);
      height: calc(100% - 6px);
      border: 3px solid #c6142d;
      top: 0;
      left: 0;
      z-index: 2;
      border-radius: 10px;
      transition: all $transitionSpeed $ease;
      opacity: 0; }
    img {
      width: 100%; } }
  &__view-item {
    font-size: 0;
    width: 19%;
    max-width: 82px;
    &.is-active {
      .general__view-image {
        &:after {
          opacity: 0; }
        &:before {
          opacity: 1; } } }
    p {
      margin-top: 4px;
      font-size: 12px;
      font-family: $light;
      margin-right: 0;
      display: inline-block;
      text-align: center;
      width: auto; } }
  .btn {
    margin-top: 30px;
    background: transparent;
    color: #c6142d;
    border: 2px solid #c6142d;
    max-width: 320px;
    width: 100%;
    text-align: center;
    font-size: 18px;
    border-radius: 3px;
    margin-left: auto;
    padding: 18px 15px;
    &:hover {
      background: #c6142d;
      color: #fff; } }
  &__left {
    max-width: 700px;
    width: 56%;
    background: #fff; }
  &__slider {
    max-width: 700px;
    width: 100%;
    position: relative;
    .slick-slide {
      font-size: 0; } }
  &__item-container {
    display: flex;
    align-items: center;
    font-size: 0; }
  &__item {
    width: 100%;
    max-height: 450px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;
    img {
      width: 100%; } }
  .slick-arrow {
    position: absolute;
    display: block;
    z-index: 2;
    top: 50%;
    transform: translateY(-50%);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 3px solid #fff;
    font-size: 0;
    background: transparent;
    cursor: pointer;
    &:before {
      position: absolute;
      display: block;
      content: '';
      width: 9px;
      height: 9px;
      border: 4px solid #fff;
      transform: translate(-50%, -50%) rotate(45deg);
      top: 50%;
      left: 50%; }
    &.slick-next {
      right: 20px;
      &:before {
        border-left: none;
        border-bottom: none;
        left: 45%; } }
    &.slick-prev {
      right: auto;
      left: 20px;
      &:before {
        border-right: none;
        border-top: none;
        left: 55%; } } }
  &__preview-item {
    width: 100%;
    max-height: 60px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2px 5px;
    cursor: pointer;
    font-size: 0;
    img {
      width: 100%;
      height: auto; } }
  &__preview-item-container {
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    max-height: 50px;
    min-height: 50px;
    &:before {
      display: block;
      position: absolute;
      content: '';
      width: calc(100% - 6px);
      height: calc(100% - 6px);
      border: 3px solid #c6142d;
      left: 0;
      top: 0;
      opacity: 0;
      transition: all $transitionSpeed $ease; } }

  &__preview-slider {
    padding: 12px 0;
    .slick-slide {
      font-size: 0; }
    .slick-current {
      .general__preview-item-container {
        &:before {
          opacity: 1; } } } } }

@include r($desktop) {
  .general {
    &__right {
      width: 37%; }
    &__left {
      width: 61%; }
    &__row {
      flex-wrap: wrap;
      padding: 10px 7px 8px 7px;
      p {
        width: 100%;
        max-width: 100%;
        &.bold {
          text-align: left;
          margin-left: 0;
          max-width: 100%;
          margin-top: 5px; } } }
    &__view-item {
      p {
        font-size: 8px; } }
    &__view-image {
      max-height: 35px; }
    &__row--price {
      p {
        &.price {
          text-align: left;
          max-width: 100%;
          margin-left: 0;
          margin-top: 5px; } } } } }

@include r($tablet) {
  .general {
    .slick-arrow {
      width: 35px;
      height: 35px;
      &:before {
        border-width: 3px;
        width: 7px;
        height: 7px; } }
    &__grid {
      flex-wrap: wrap; }
    &__right {
      width: 100%;
      max-width: 100%;
      margin-top: 15px; }
    &__left {
      width: 100%;
      max-width: 100%; }
    &__preview-slider {
      display: none; }
    &__view-image {
      max-height: 40px; }
    .btn {
      margin: 20px auto 0;
      font-size: 16px;
      padding: 13px 15px; }
    &__title {
      font-size: 18px; }
    &__row {
      &--price {
        p {
          &.price {
            font-size: 32px; } } } } } }
