.firstscreen {
  background: #000;
  // padding: 186px 0 165px
  background: url(../img/bg-main.jpg) center bottom no-repeat;
  background-size: cover;
  min-height: calc(100vh - 86px);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  overflow: hidden;
  padding: 40px 0;
  &__carousel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0,0,0,0.1);
    font-size: 0; }
  &__carousel-item {
    width: 100%;
    height: 100%;
    background-size: cover;
    outline: 1px solid red;
    min-height: 100vh; }
  .container {
    max-width: 1532px;
    position: relative;
    z-index: 2; }

  .btn {
    max-width: 374px;
    width: 100%;
    margin-top: 43px;
    position: relative;
    z-index: 2; }
  &__content {
    max-width: 576px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    img {
      width: 100%;
      max-width: 576px; } }
  &__pattern {
    max-width: 726px;
    width: 80%;
    height: auto;
    position: absolute;
    opacity: 0.25;
    bottom: -120px;
    left: -250px; } }

@include r($hd1600) {
  .firstscreen {
    min-height: calc(100vh - 80px); } }

@include r($desktop) {
  .firstscreen {
    min-height: calc(100vh - 70px); } }

@include r($tablet) {
  .firstscreen {
    // min-height: 610px
    // min-height: calc(100vh - 70px)
    min-height: 0;
    align-items: flex-start;
    padding: 125px 0 85px;
    &__content {
      padding: 0 10px;
      max-width: 400px; }
    .btn {
      font-size: 12px;
      padding: 10px 16px;
      width: auto;
      margin-right: 2px;
      margin-top: 18px; }
    &__pattern {
      left: -350px;
      bottom: -100px;
      width: 150%; } } }
